@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;600&family=Playfair+Display:wght@700&family=Raleway:wght@400;700&family=Roboto:wght@300&display=swap');

* {
  margin: 0;
  padding: 0;
}

body {
  background: #fff;
  color: #222;
  font-family: 'Montserrat', sans-serif;
  font-weight: 300;
}

.wrapper {
  width: 1280px;
  margin: 50px auto;
}

header {
  position: relative;
}

header .logo {
  font-weight: 600;
  font-size: 20px;
}

header ul.nav {
  float: right;
  list-style: none;

}

header ul.nav li {
  display: inline;
  margin-left: 25px;
  cursor: pointer;
  transition: opacity 500ms ease;
}

header ul.nav li:hover {
  opacity: 0.5;
}

header .shop-card-button {
  float: right;
  cursor: pointer;
  transition: color transform 500ms ease;
}

header .shop-card-button:hover,
header .shop-card-button.active {
  color: #dc3d3d;
  transform: scale(1.5);
}

header .shop-card {
  position: absolute;
  top: 30px;
  right: 0;
  width: 450px;
  background: #fafafa;
  -webkit-box-shadow: 4px 5px 15px -7px #000000;
  box-shadow: 4px 5px 15px -7px #000000;
  z-index: 1000;
  padding: 20px;
  padding-bottom: 0;

}

header .shop-card .item {
  width: 100%;
  float: left;
  margin-bottom: 40px;
}

header .shop-card .empty {
  font-size: 20px;
  margin-bottom: 20px;
}

header .shop-card .item img {
  width: 70px;
  float: left;
  margin-right: 20px;
}

header .shop-card .item h2 {
  font-size: 20px;
  margin-bottom: 10px;
}

header .shop-card .item p {
  color: #797979;
  font-weight: 600;
}

header .shop-card .item .delete-icon {
  float: right;
  color: #ca5252;
  top: -25px;
  position: relative;
  cursor: pointer;
  transition: color transform 500ms ease;
}

header .shop-card .item .delete-icon:hover {

  color: #dc3d3d;
  transform: scale(1.5);
}

header .shop-card .summa{
  float: left;
  width: 100%;
  font-weight: 600;
  font-size: 20px;
  margin-bottom: 20px;
}

header .presentation {
  margin: 50px 0;
  background: url('./img/background.png') no-repeat;
  width: 100%;
  height: 500px;
  background-size: cover;
  background-position: center center;
  background-blend-mode: multiply;
  background-color: #bcbcbc;
  position: relative;
}

header .presentation::after {
  content: 'Лучшие товары для вашего дома';
  position: absolute;
  top: 100px;
  left: 50px;
  width: 300px;
  font-size: 40px;
  font-weight: 600;
  color: #fff;
}

header .presentation::before {
  content: 'По низким ценам';
  position: absolute;
  top: 250px;
  left: 50px;
  width: 300px;
  font-size: 15px;
  font-weight: 300;
  color: #fff;
}

.categories div {
  display: inline-block;
  border-radius: 50px;
  margin-bottom: 25px;
  margin-right: 15px;
  background: #f2f2f2;
  padding: 10px 20px;
  cursor: pointer;
  border: 1px solid transparent;
  transition: all 500ms ease;
}
.categories div:hover{
  border-color: silver;
  transform: scale(1.1);
}

.full_item{
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  z-index: 9999;
  overflow: auto;
}
.full_item > div{
width: 600px;
position: relative;
padding: 40px 30px;
border-radius: 9px;
margin: 10% auto;
background: #fff;
}

main {
  color: black;
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  justify-content: space-between;
}

main .item {
  width: 30%;
  margin-bottom: 50px;
  background: #f5f5f5;
  position: relative;
  padding-bottom: 20px;

}

main .item img, .full_item img{
  width: 100%;
  border-radius: 10px 10px 0 0;
  transition: transform 500ms ease;
  cursor: pointer;
}

main .item img:hover, 
.full_item img:hover{
  transform: scale(1.05);
}

main h2, .full_item h2,
main p, .full_item p,
main b, .full_item b {
  margin: 10px 20px;
  color: #333;
}

main b,
.full_item b{
  color: #5fa36a;

}

main .add-to-card,
.full_item .add-to-card {
  position: absolute;
  right: 20px;
  bottom: 20px;
  background: #ca5252;
  width: 35px;
  height: 35px;
  text-align: center;
  line-height: 35px;
  color: #fff;
  border-radius: 50%;
  cursor: pointer;
  font-weight: 600;
  transition: transform 500ms ease;
}

main .add-to-card:hover,
.full_item .add-to-card:hover{
  transform: scale(1.05) translateY(-5px);
}

footer {
  text-align: center;
  height: 200px;
}